/* BRAND BOX */
.Brand_sheet_box {
  background-color: #fff;
  display: flex;

  .Brand_outer {
    width: 20%;
    height: 500px;
    padding: 10px;
    box-sizing: border-box;

    a {
      display: block;

      img {
        width: 100%;
        height: auto;
        display: block;
      }

      p {
        width: 100%;
        height: 30px;
        line-height: 30px;
        text-align: center;
        font-weight: bold;
        padding-top: 15px;
      }
    }
  }
}

.hd_fix {
  position: fixed;
  top: 0;
  left: 0;
  background-color: $PointColor;
  z-index: 99999;
}




// 카테고리 메뉴 IMG 및 TXT
.TopArea {
  position: relative;
  z-index: 5;
  margin-top: 100px;

  .categoryBox{
    position: relative;
    width: 1440px;
    height: 350px;
    margin: 0 auto;
    background-image: url(./BACK_IMG/Fila_Category.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.4);
    background-blend-mode: hue;

    .categoryIMG {
      width: 100%;
      height: 100%;
    }

    ul{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-evenly;
      align-items: center;
      
      width: 215px;
      height: 80px;
      position: absolute;
      left: 0;
      bottom: 0;
      color: #fff;

      li{
        font-size: 32px;
        font-weight: bold;
      }

      li:nth-child(2) {
        padding-top: 5px;
        font-size: 16px;
      }
    }

    img {
      width: 100%;
      height: auto;
      display: block;
    }
  }

  .filter {
    position: relative;
    width: 1440px;
    height: 50px;
    margin: 0 auto;
    background-color: #fff;

    border-bottom: 1px solid #eee;
    box-sizing: border-box;


    .filterBox {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      height: 50px;

      .filterSide1 {
        .filterSide_block1,
        .filterSide_block2 {

          width: 120px;

          span {
            padding: 0 20px;
            color: #000;
            font-weight: bold;
            cursor: pointer;
          }

          span:nth-child(2),
          span:nth-child(3) {
            width: 14px;
            padding: 0px;
            color: $PointColor;
          }

          span:nth-child(3) {
            display: none;
          }
        }
        .filterSide_block2 {
          display: none;
        }
      }
      .filterSide2 {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        width: 250px;
        height: 50px;

        select {
          outline: none;
          border: none;
          background-color: #fff;
          text-align: center;
          margin-right: 20px;
          cursor: pointer;
        }
        span {
          display: inline-block;
          padding: 0 20px;
          cursor: pointer;

          img {
            width: 100%;
            height: auto;
            display: block;
          }
        }
        span:nth-child(2) {
          position: relative;
          z-index: -5;
          width: 1px;
          height: 18px;
          border-right: 1px solid #aaa;padding: 0;
          cursor: auto;
        }
        span:nth-child(4) {
          padding-left: 0px;
        }
      }
    }
    .filter_menuDown {
      position: absolute;
      top: 50px;
      left: 0;
      display: flex;
      // display: none;
      width: 1440px;
      // height: 400px;
      height: 0px;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      border-bottom: 1px solid #eee;
      box-sizing: border-box;
      overflow: hidden;
      background-color: #fff;

      .Ft_Arr {
        width: 223.3333px;
        height: 100%;
        h1 {
          width: 100px;
          margin: 0 auto;
          text-align: center;
          padding: 15px 0;
          font-size: $LageFont;
        }
        ul {
          height: 400px;

          li {
            width: 110px;
            height: 40px;
            margin: 0 auto;
            cursor: pointer;

            input {
              cursor: pointer;
            }

            label {
              cursor: pointer;
            }
          }
        }
      }

      .Filter_ALL {
        ul {
          li:hover {
            color: $PointColor;
            font-weight: bold;
            transition: 0.3s;
          }
        }
      }

      .Filter_Sports {
        ul {
          width: 100%;

          li {
            width: 110px;
            margin: 0 auto;

            label:hover {
              color: $PointColor;
              font-weight: bold;
              transition: 0.3s;
            }
          }
        }
      }

      .Filter_Color {
        div {
          width: 38px;
          height: 38px;
          margin-right: 10px;
          float: left;
          border-radius: 50%;
          cursor: pointer;
          margin: 0 10px;
        }
        div:nth-child(2) {
          background-image: url(./BACK_IMG/RED.png);
          background-size: contain;
          background-repeat: no-repeat;
          margin-left: 5px;
        }
        div:nth-child(3) {
          background-image: url(./BACK_IMG/BLUE.png);
          background-size: contain;
          background-repeat: no-repeat;
        }
        div:nth-child(4) {
          background-image: url(./BACK_IMG/GREEN.png);
          background-size: contain;
          background-repeat: no-repeat;
        }
        div:nth-child(5) {
          background-image: url(./BACK_IMG/YELLOW.png);
          background-size: contain;
          background-repeat: no-repeat;
          margin-right: 0;
        }
        div:hover {
          border: 2px solid #000;
          box-sizing: border-box;
        }
      }

      .Filter_Size {
        padding: 0 10px;
        box-sizing: border-box;
        div {
          width: 43px;
          height: 43px;
          float: left;
          margin-right: 10px;
          margin-bottom: 10px;
          border: 1px solid #ccc;
          box-sizing: border-box;
          border-radius: 10px;
          text-align: center;
          line-height: 43px;
          cursor: pointer;
        }
        div:nth-child(5),
        div:nth-child(9),
        div:nth-child(13) {
          margin-right: 0;
        }
        div:hover {
          border: 1px solid $PointColor;
          box-sizing: border-box;
        }
      }

      .Filter_Price {
        ul {
          li {
            width: 165px;

            label:hover {
              font-size: 15.2px;
              color: $PointColor;
              font-weight: bold;
              box-sizing: border-box;
            }
          }
        }
      }

      .Filter_Gender {
        ul {
          li {
            width: 55px;

            label:hover {
              color: $PointColor;
              font-weight: bold;
            }
          }
        }
      }

      .ft_bar {
        width: 1px;
        height: 300px;
        margin: 50px 0;
        border-right: 1px solid #eee;
        box-sizing: border-box;
      }
    }
  }
}






// ITEM ARRAY 

.item_Array_box1  {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  width: 1440px;
  margin: 0 auto;
  margin-top: 50px;

  .item_Arr {
    width: 345px;
    height: 465px;
    margin-bottom: 20px;
    overflow: hidden;

    img {
      display: inline;
      width: 100%;
      height: auto;
      display: block;
    }
  }

  .item_bt {
    width: 100%;
    height: 120px;

    .btm_txt {
      width: 68%;
      height: 120px;
      float: left;
      background-color: skyblue;

      p {
        margin-top: 5px;
        font-size: 14px;
      }
      h3 {
        padding-top: 5px;
        height: 60px;
        box-sizing: border-box;
      }
      h4 {
        color: $PointColor;
      }
    }
    .btm_btn {
      width: 32%;
      height: 120px;
      padding: 10px;
      float: right;
      box-sizing: border-box;

      div {
        margin-top: 10px;
        width: 30px;
        height: 30px;
        margin-left: 2px;
        margin-right: 15px;
        float: left;
        cursor: pointer;

        img {
          width: 100%;
          height: auto;
          display: block;
        }
      }
      div:nth-child(2) {
        padding-top: 2px;
        margin-right: 2px;
        float: right;
        box-sizing: border-box;
      }

      input {
        display: none;
      }
      label {
        display: inline-block;
        width: 90px;
        height: 40px;
        text-align: center;
        line-height: 35px;
        border: 2px solid #000;
        box-sizing: border-box;
        border-radius: 30px;
        margin-top: 20px;
        font-size: 14px;
        font-weight: bold;
        cursor: pointer;
      }
      label:hover {
        background-color: $PointColor;
        border: 2px solid $PointColor;
        color: #fff;
        transition: 0.3s;
      }
    }
  }
}











